<template>
  <div class='main-box'>
    <div class="home-slide-img">
      <slideImg></slideImg>
    </div>
    
    <div class="main-function">
      <van-grid clickable :column-num="2">
        <!-- <van-grid-item icon="home-o" text="路由跳转" to="/" /> -->
        <van-grid-item icon="home-o" text="预约挂号" to="/selectDepartment"/>
        <van-grid-item icon="search" text="门诊缴费"/>
      </van-grid>
    </div>
    <div class="features-graid">
      <van-grid square :column-num="3">
        <van-grid-item
          v-for="(item,index) in gridTextList"
          :key="index"
          icon="photo-o"
          :text="item.text"
          @click="$router.push({path:item.url})"
        />
      </van-grid>
    </div>
  </div>
</template>

<script>
import slideImg from '../../common/bannerslide'
  export default {
    name: 'home',
    data() {
      return{
        gridTextList:[
          {text:'排队候诊',url:"lineup"},
          {text:'报告查询',url:''},
          {text:'智能导诊',url:''},
          {text:'来院导航',url:'navigation'},
          {text:'就诊指南',url:''},
          {text:'医院概况',url:''},
          {text:'门诊满意度调查',url:''},
          {text:'医院动态',url:''},
          {text:'门诊缴费(订单)',url:""}
        ]
      }
    },
    components:{slideImg}
  };
</script>
<style lang='scss' scoped>
  .home-slide-img{height: auto;}
  .main-function{
    width: 100%;
    margin-top: 10px;
  }
  .features-graid{margin-top: 20px;}
</style>


