<template>
  <div class="slide-img">
    <van-swipe 
    :autoplay="0" 
    indicator-color="white" 
    >
      <van-swipe-item><img src="../../assets/img/ab_banner1.png"></van-swipe-item>
      <van-swipe-item><img src="../../assets/img/ab_banner2.png"></van-swipe-item>
      <van-swipe-item><img src="../../assets/img/ab_banner3.png"></van-swipe-item>
    </van-swipe>
  </div>
    
</template>

<script>
  export default {
    name: '',
  };
</script>
<style lang='scss' scoped>
.slide-img{
  width: 100%;
  img{
    width: 100%;
    height: 100%;
  }
}
</style>


